<template>
	<div class="p-grid">
		<div class="p-col-12">
			<div class="card">
				<h5>Networth : S$100,000</h5>
				<TreeTable :value="treeTableValue" sortMode="multiple" >
					<template #header>
						Details
					</template>
					<Column field="bank" header="Bank" :expander="true"></Column>
					<Column field="currency" header="Currency" :sortable="true"></Column>
					<Column field="amount" header="Amount" :sortable="true"></Column>
				</TreeTable>
			</div>
		</div>
	</div>
</template>

<script>
	import NodeService from '../service/NodeService'

	export default {
		data() {
			return {
				treeValue: null,
				selectedTreeValue: null,
				treeTableValue: null,
				selectedTreeTableValue: null,
			}
		},
		nodeService: null,
		created() {
			this.nodeService = new NodeService();
		},
		mounted() {
			this.nodeService.getTreeNodes().then(data => this.treeValue = data);
			this.nodeService.getTreeTableNodes().then(data => this.treeTableValue = data);
		},
	}
</script>

